@import url('https://fonts.googleapis.com/css?family=Press+Start+2P');

@keyframes bgfade {
  0% {
    background-color: #1f1a45;
  }

  50% {
    background-color: 	#2c5881;
  }

  100% {
    background-color: #1f1a45;
  }
}

@keyframes textflicker {
  0% { opacity: 0.5; }
  10% { opacity: 0.25; }
  20% { opacity: 0.5; }
  30% { opacity: 1; }
  40% { opacity: 0.25; }
  50% { opacity: 0.5; }
  60% { opacity: 0.25; }
  70% { opacity: 0.5; }
  80% { opacity: 1; }
  90% { opacity: 0.25; }
  100% { opacity: 0.75; }
}

body {
  animation-duration: 10s;
  animation-name: bgfade;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.app {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Press Start 2P', cursive;
  color: #bcf982;

  span {
    animation-name: textflicker;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    padding: 10px 0 10px 0;

    &:first-child {
      animation-duration: 0.15s;
      font-size: 36px;
    }

    &:last-child {
      animation-duration: 0.1s;
      font-size: 16px;
    }
  }
}